import React, { useState, useEffect } from 'react';
import axios from 'axios';

function TwoDropdowns() {
  const [isOpen1, setIsOpen1] = useState(false);
  const [selectedValue1, setSelectedValue1] = useState('');
  const [isOpen2, setIsOpen2] = useState(false);
  const [selectedValue2, setSelectedValue2] = useState('');
  const [options1, setOptions1] = useState([]);
  const [options2, setOptions2] = useState([]);

  useEffect(() => {
    axios.post('/allprojectsapi')
      .then(response => {
        console.log(response.data.map(item=>item.projectname))
        setOptions1(response.data.map(item=>item.projectname));
        // setOptions2(response.data.options2);
      })
      .catch(error => console.log(error));
  }, []);

  const handleOptionClick1 = (projectname) => {
    setSelectedValue1(projectname);
    setIsOpen1(false);
  }

  const handleOptionClick2 = (projectname) => {
    setSelectedValue2(projectname);
    setIsOpen2(false);
  }

  const renderOptions1 = () => {
    return options1.map((projectname, index) => {
      return (
        <div key={index} onClick={() => handleOptionClick1(projectname)}>
          {projectname}
        </div>
      );
    });
  }

  const renderOptions2 = () => {
    return options2.map((projectname, index) => {
      return (
        <div key={index} onClick={() => handleOptionClick2(projectname)}>
          {projectname}
        </div>
      );
    });
  }

  return (
    <div>
      <div className="button-dropdown-container">
        <button onClick={() => setIsOpen1(!isOpen1)}>
          {selectedValue1 ? selectedValue1 : 'Select an option'}
        </button>
        {isOpen1 && (
          <div className="button-dropdown-options">
            {renderOptions1()}
          </div>
        )}
      </div>
      <div className="button-dropdown-container">
        <button onClick={() => setIsOpen2(!isOpen2)}>
          {selectedValue2 ? selectedValue2 : 'Select an option'}
        </button>
        {isOpen2 && (
          <div className="button-dropdown-options">
            {renderOptions2()}
          </div>
        )}
      </div>
    </div>
  );
}

export default TwoDropdowns;
