import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'

const Navbaremp = () => {

  const [data,setData]= useState([])
  useEffect (
      ()=>{
          fetchProfile()
      }
  )
  const fetchProfile =()=>
{
  const data= {"_id": sessionStorage.getItem("userid")}
  axios.post("/profileapi",data)
  .then(
      (res)=>{
          setData(res.data)
      }
  )
}
    let Navigate = useNavigate()
    const btn3=()=>{
        sessionStorage.removeItem("userid")
        Navigate("/login")
    }
  return (
    <div><nav class="navbar navbar-expand-lg bg-c-lite-green navbar-dark">
    <div class="container-fluid">
      <a class="navbar-brand" href="#"><img src="data:image/png;base64," alt="" width="60" 
              height="60" /> <font size="6" >  
              ICTAK CLOCK-IN SYSTEM
          </font> </a>
      
      <div class="nav navbar-right ">
      <div class=" text-center text-white">
                                                               
        <h5 className="whiteTextOverride">{data.ename}</h5>
        <font size="2">{data.erole}</font>
                                                               
                                                           </div>
      <div class="collapse navbar-collapse " id="navbarNav">
        <ul class="navbar-nav ">
          
          <li class="nav-item"><div className="btn-signout">
            <button className=" margin-left" onClick={btn3}>Sign out</button></div>
          </li>
        
        </ul>
      </div></div>
    </div>
  </nav></div>
  )
}

export default Navbaremp