import axios from 'axios';
import React, { useEffect, useState } from 'react'


const Timetracker = () => {
  
  


    
  const [hours, setHours] = useState(0);
  const [minutes, setMinutes] = useState(0);
  const [seconds, setSeconds] = useState(0);
  const [isActive, setIsActive] = useState(false);
  const [userid,setUserid]=useState(sessionStorage.getItem('userid'));
  const [token,setToken]=useState(sessionStorage.getItem('userToken'));
  const [projectname,setProjectname]=useState('');
  const [taskname,setTaskname]=useState('');
  const [mode,setMode]=useState('')
  const [jobdesc,setJobdesc]=useState('')



  const [isOpen1, setIsOpen1] = useState(false);
  const [selectedValue1, setSelectedValue1] = useState('');
  const [isOpen2, setIsOpen2] = useState(false);
  const [selectedValue2, setSelectedValue2] = useState('');
  const [isOpen3, setIsOpen3] = useState(false);
  const [selectedValue3, setSelectedValue3] = useState('');
  const [options1, setOptions1] = useState([]);
  const [options2, setOptions2] = useState([]);
  const [options3, setOptions3] = useState([]);
  const [selectedTracker, setSelectedTracker] = useState(null);


  useEffect(() => {
    axios.post("/allprojectsapi")
      .then(response => {
        setOptions1(response.data.map(item=>item.projectname));
      })
      .catch(error => console.log(error));
  }, []);

  useEffect(() => {
    axios.post("/alltasksapi")
      .then(response => {
        setOptions2(response.data.map(item=>item.taskname));
      })
      .catch(error => console.log(error));
  }, []);

  useEffect(() => {
      const modes=["Work from Office","Work from Home"]
      setOptions3(modes)
  }, []);

  


  var [allTrackers,setallTracker]=useState([])
  useEffect(
      ()=>{
          viewallTracker()
      }
      )
      const viewallTracker=()=>{
        
          const data ={"userid":sessionStorage.getItem("userid")}
          axios.post("/viewalltrackersapi",data)
          
          .then(
              (res)=>{
                  
                  setallTracker(res.data)
              }
          ).catch()
          
      }
      const handleResume=(tracker)=>{
        setSelectedTracker(tracker);
    setHours(tracker.hours);
    setMinutes(tracker.minutes);
    setSeconds(tracker.seconds);
    setProjectname(tracker.projectname);
    setTaskname(tracker.taskname);
    setJobdesc(tracker.jobdesc);
    setMode(tracker.mode);
    setSelectedValue1(tracker.projectname);
    setSelectedValue2(tracker.taskname);
    setSelectedValue3(tracker.mode);
      
      }
  const handleOptionClick1 = (option) => {
    setProjectname(option)
    setSelectedValue1(option);
    setIsOpen1(false);
  }

  const handleOptionClick2 = (option) => {
    setTaskname(option)
    setSelectedValue2(option);
    setIsOpen2(false);
  }
  
  const handleOptionClick3 = (option) => {
  
    setMode(option);
    setSelectedValue3(option);
    setIsOpen3(false);
  }

  const renderOptions1 = () => {
    return options1.map((option, index) => {
      return (
        <div key={index} onClick={() => handleOptionClick1(option)}>
          {option}
        </div>
      );
    });
  }

  const renderOptions2 = () => {
    return options2.map((option, index) => {
      return (
        <div key={index} onClick={() => handleOptionClick2(option)}>
          {option}
        </div>
      );
    });
  }


  const renderOptions3 = () => {
    return options3.map((option, index) => {
      return (
        <div key={index} onClick={() => handleOptionClick3(option)}>
          {option}
        </div>
      );
    });
  }



  

  useEffect(() => {
    let interval = null;
    let initialSeconds = seconds;
    let initialMinutes = minutes;
    let initialHours = hours;
    if (isActive) {
      interval = setInterval(() => {
        if (initialSeconds === 59) {
          if (initialMinutes === 59) {
            initialHours++;
            initialMinutes = 0;
          } else {
            initialMinutes++;
          }
          initialSeconds = 0;
        } else {
          initialSeconds++;
        }
        setSeconds(initialSeconds);
        setMinutes(initialMinutes);
        setHours(initialHours);
      }, 1000);
    } else if (!isActive && seconds !== 0) {
      clearInterval(interval);
    }
    return () => clearInterval(interval);
  }, [isActive, seconds, minutes, hours, selectedTracker]);
  
  const handleStart = () => {
    if (!projectname || !taskname || !mode || !jobdesc) {
      alert("Please select a project, task and mode of work and fill the job description before clicking start.");
      return;
    }
    setIsActive(true);
  };

  const handlePause = () => {
    setIsActive(false);
  };


  const handleReset = () => {
    if (!projectname || !taskname || !mode || !jobdesc) {
      alert("Please select a project, task and mode of work and fill the job description before clicking stop.");
      return;
    }
    
    if (selectedTracker) {
      // Update existing tracker with same _id
      const data = {
        userid: userid,
        token: token,
        hours: hours ,
        minutes: minutes ,
        seconds: seconds ,
        projectname: projectname,
        taskname: taskname,
        mode: mode,
        jobdesc: jobdesc,
        _id: selectedTracker._id // add _id of selected tracker to update it
      };
      axios.put(`/trackerapi/${selectedTracker._id}`, data)
        .then((response) => {
          console.log(response);
          if (response.data.status === "unauthorized user") {
            alert("Login First");
            selectedTracker=null
          } else {
            if (response.data.status === "Tracker updated successfully") {
              
              alert("Tracker updated successfully");

            } else {
              alert("something went wrong");
            }
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      // Create new tracker
      const data = {
        userid: userid,
        token: token,
        hours: hours,
        minutes: minutes,
        seconds: seconds,
        projectname: projectname,
        taskname: taskname,
        mode: mode,
        jobdesc: jobdesc,
      };
      axios.post("/trackerapi", data)
        .then((response) => {
          console.log(response);
          if (response.data.status === "unauthorized user") {
            alert("Login First");
          } 
        })
        .catch((error) => {
          console.log(error);
        });
    }
    setHours(0);
    setMinutes(0);
    setSeconds(0);
    setIsActive(false);
    setProjectname('');
setTaskname('');
setMode('');
setJobdesc('');
setSelectedTracker(null);
setSelectedValue1('')
setSelectedValue2('')
setSelectedValue3('')
  };

  const getTotalTime = (trackers) => {
    let totalTime = { hours: 0, minutes: 0, seconds: 0 };
    trackers.forEach((tracker) => {
      totalTime.hours += parseInt(tracker.hours);
      totalTime.minutes += parseInt(tracker.minutes);
      totalTime.seconds += parseInt(tracker.seconds);
    })

}
let totalTimesec = allTrackers.reduce(
    (totalTime, tracker) =>
      totalTime +
      tracker.hours * 3600 +
      tracker.minutes * 60 +
      tracker.seconds,
    0
  )
let totalTimehours = parseInt(totalTimesec / 3600);
totalTimesec = totalTimesec % 3600
let totalTimeminutes = parseInt(totalTimesec / 60)
totalTimesec = totalTimesec % 60

  

  return (

    
    <div className="container">
      
      <div className="row">
       
      <div className="wrapper">
      <div className='container1'>
          
            
          
           <div className='box2'>
      <div className="button-dropdown-container">
        <button onClick={() => setIsOpen1(!isOpen1)}>
          {selectedValue1 ? selectedValue1 : 'Select a project'}
        </button>
        {isOpen1 && (
          <div className="button-dropdown-options">
            {renderOptions1()}
            <a href="/empaddproject"> + Add Project</a>
          </div>
        )}
      </div>
      </div>
          <div className="box2">
      <div className="button-dropdown-container">
        <button onClick={() => setIsOpen2(!isOpen2)}>
          {selectedValue2 ? selectedValue2 : 'Select a task'}
        </button>
        {isOpen2 && (
          <div className="button-dropdown-options">
            {renderOptions2()}
            <a href="/empaddtask">+ Add Task</a>
          </div>
        )}
      </div>
    </div>
    <div className='box2'>
      <div className="button-dropdown-container">
        <button onClick={() => setIsOpen3(!isOpen3)}>
          {selectedValue3 ? selectedValue3 : 'Mode of work'}
        </button>
        {isOpen3 && (
          <div className="button-dropdown-options">
            {renderOptions3()}
          </div>
        )}
      </div>
      </div>
      <div className="box2">
        <div className="row">
          <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
            <input 
            type="text"
            className="form-control" 
            placeholder='Job Description'
            onChange={(e)=>setJobdesc(e.target.value)}
            name="jobdesc"
            value={jobdesc}/>
          </div>
        </div>
      </div>
{/* <div className="box2">
      <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p>
      </div> */}

           </div>
           
          <div className="wrapper1 text-end" >
          <div className="box1">
          <div className="btn-group">
            <button className="btn btn-success" onClick={handleStart}>Start</button>
            <button className="btn btn-primary" onClick={handlePause}>Pause</button>
            <button className="btn btn-danger" onClick={handleReset}>Stop</button>
            </div>
            </div>
          <div className='box1'>
            
          <h2>{hours}:{minutes < 10 ? '0' + minutes : minutes}:{seconds < 10 ? '0' + seconds : seconds}</h2>
          </div>
          </div>
          </div>
        </div>
        <div>
       
        <table class="table table-hover">
  <thead>
    <tr>
      <th class="table-dark" scope="col">Date</th>
      <th class="table-dark" scope="col">Project</th>
      <th class="table-dark"scope="col">Task</th>
      <th class="table-dark" scope="col">Mode of work</th>
      <th class="table-dark" scope="col">Job description</th>
      <th class="table-dark" scope="col">Working Time</th>
      <th class="table-dark" scope="col">Resume</th>
    </tr>
  </thead>
  <tbody>
    {allTrackers.map(
        (value,index)=>{
            
            return <tr>
            <td class="table-light">{new Date(value.addeddate).toLocaleDateString('en-GB')}</td>
            <td class="table-light">{value.projectname}</td>
            <td class="table-light">{value.taskname}</td>
            <td class="table-light">{value.mode}</td>
            <td class="table-light">{value.jobdesc}</td>
            <td class="table-light">{value.hours}:{value.minutes < 10 ? '0' + value.minutes : value.minutes}:{value.seconds < 10 ? '0' + value.seconds : value.seconds}</td> 
            <td class="table-light"><button className='btn5'  onClick={() => handleResume(value)}>Resume</button></td>
          </tr>
        }
    )}
   <tr>
          <th class="table-dark" colSpan="5" >Total Time Worked:</th>
          <td class="table-light">{totalTimehours}:{totalTimeminutes < 10 ? '0' + totalTimeminutes : totalTimeminutes}:{totalTimesec < 10 ? '0' + totalTimesec : totalTimesec}
          </td>
        </tr>
   
  </tbody>
</table>
    </div>
        </div>
        
      
  );

}

export default Timetracker