import React from 'react'
import { useNavigate } from 'react-router-dom'

const Navbaradmin = () => {
    let Navigate = useNavigate()
    const btn=()=>{
        Navigate("/adminaddemp")
    }
    const btn2=()=>{
      Navigate("/adminaddproortask")
    }
    const btn3=()=>{
      Navigate("/adminlogin")
      
    }
  return (
    <div><nav class="navbar navbar-expand-lg bg-c-lite-green navbar-dark">
    <div class="container-fluid">
      <a class="navbar-brand" href="#"><img src="data:image/png;base64," alt="" width="60" 
              height="60" /> <font size="6" ><strong>  
              ICTAK CLOCK-IN SYSTEM</strong>
          </font> </a>
      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="nav navbar-right g-3">
      <div class="collapse navbar-collapse " id="navbarNav">
        <ul class="navbar-nav ">
          
          <li class="nav-item">
            <div className="btn-container2">
            <button  onClick={btn}>Add New Employee</button>
            </div>
          </li>
          <li class="nav-item"><div className="btn-container2">
          <button className="margin-left"onClick={btn2}>Add New Project/Task</button>
          </div> </li>
          <li class="nav-item"><div className="btn-signout">
            <button className=" margin-left" onClick={btn3}>Sign out</button>
            </div>
          </li>
        
        </ul>
      </div></div>
    </div>
  </nav></div>
  )
}

export default Navbaradmin