import React from 'react'
import { useNavigate } from 'react-router-dom'
import Navbarhome from './Navbarhome'

const Home = () => {
    let Navigate= useNavigate()
const emplogin=()=>{
    Navigate('/login')
}
const adminlogin=()=>{
    Navigate('/adminlogin')
}
  return (
    <div>
        <Navbarhome/>
        
            <div className="container11">
            <div className="row">
            
            
            <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
            <div className="">
            <div className="row g-3">
            
                <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                    <h4>ICTAK Clock-in System is a reliable and efficient way to track the working hours of employees at ICTAK. Our user-friendly interface makes clocking in and out a breeze, while our automated reporting helps employers keep track of attendance.</h4>
                </div>
                <div className="col col-12 col-sm-12 col-md-12 col-lg-6 col-xl-6 col-xxl-6">
                <div className="btn-container7"><button  class ="block btn btn-1" onClick={emplogin} >Employee Login</button></div>
                </div>
                <div className="col col-12 col-sm-12 col-md-12 col-lg-6 col-xl-6 col-xxl-6">
                <div className="btn-container7"><button  class ="block btn btn-1" onClick={adminlogin} >Admin Login</button></div>
                </div>
                </div></div></div>
            </div>
        </div>
    </div>
  )
}

export default Home