import axios from 'axios'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Navbar from './Navbar'
import './MyComponent.css';
import { faEnvelope, faLock } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';



const Login = () => {
    
    let Navigate = useNavigate()
    const[data,setData]= useState(
        {eemail: "",
        epassword:""
        })
        const inputHandler=(event)=>{
            const {name,value}=event.target
            setData(
                (previousState)=>({
                    ...previousState,
                    [name]:value
                })
            )
        }
        const readValue=()=>{
            console.log(data)
            axios.post("/loginapi",data)
            .then((response)=>{
                console.log(response.data)
                if (response.data.status=="success"){
                    let token=response.data.token
                    let userid=response.data.data[0]._id
                    // alert("valid user")
                    // alert("userid"+ userid)
                    // alert("token"+ token)
                    sessionStorage.setItem("userToken",token)
                    sessionStorage.setItem("userid",userid)
                    Navigate("/viewtrackerandlist")
                  
                }else{
                    alert("invalid user")
                }
               
            })
            .catch((error)=>{
                console.log(error)
            })
        }
    
  return (
    <div>
   {/* <div style={{ backgroundImage: `url(https://images.hdqwalls.com/download/time-clock-digital-creative-illustration-2d-2560x1440.jpg)` }}> */}
        <Navbar />
       
    <div className="container" >
    
        
        <div className="row">
            
            
            <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
            <div className="my-component">
            <div className= "loginalign">
            <div className="row g-4">
                <h3>Employee Login</h3>
            
                
                <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                    
                    <input 
                    type="text" 
                    className="form-control" 
                    placeholder='Email'
                    onChange={inputHandler}
                    value={data.eemail}
                    name="eemail" />
                    
                </div>
                <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                    <input 
                    type="password" 
                    id="" 
                    className="form-control" 
                    placeholder='Password'
                    onChange={inputHandler}
                    value={data.epassword}
                    name ="epassword" />
                     
                </div>
                <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                
                <div className="btn-container7"><button  class ="block btn btn-1"  onClick={readValue}>Login</button></div>
                    
                    
                </div>
            </div>
            </div>
            </div>
            </div>
        </div>
        </div>
    </div>
  )
}

export default Login