import axios from 'axios';
import React, { useCallback, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { Navbaradempprofile } from './Navbaradempprofile';

const Adminupdateemp = () => {
    let Navigate = useNavigate()
    const [data,setData]= useState({ ename: '', erole: '', eemail: '' })

    useEffect(() => {
        fetchEmployee()
      }, [])
      const fetchEmployee = () => {
        const data = { _id: sessionStorage.getItem("userid") };
        axios.post("/updateapi", data).then((res) => {
          setData(res.data);
        });
      };
    const handleInputChange = (event) => {
        console.log(event)
        const { name, value } = event.target;
        setData((prevEmployee) => ({
          ...prevEmployee,
          [name]: value,
        }));
      };


    const handleSave =  () => {
        console.log(data)
        axios.post("/makeupdateapi",data)
        .then((response)=>{
            console.log(response.data)
            if (response.data.Status == "Success") {
                
                Navigate("/adminviewemp")                
            } else {
                
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    }
  
    return (
      <div>
        <Navbaradempprofile/>
        
        <div className= "addalign">
        <div className="row g-3">
        <h3>Update Employee Details</h3>
        
        <div>
          <label htmlFor="ename"><h5>Name</h5></label>
          <input
        type="text"
         name="ename"
         id="ename"
         className='form-control'
          onChange={handleInputChange}
         value={data.ename}
/>

        </div>
        <div>
          <label htmlFor="erole"><h5>Role</h5></label>
          <input
            type="text"
            name="erole"
            id="erole"
            className='form-control'
            value={data.erole }
            onChange={handleInputChange}
          />
        </div>
        <div>
          <label htmlFor="eemail"><h5>Email</h5></label>
          <input
            type="email"
            name="eemail"
            id="eemail"
            className='form-control'
            value={data.eemail }
            onChange={handleInputChange}
          />
        </div>
        <div>
          <label htmlFor="ephnnum"><h5>Phone Number</h5></label>
          <input
            type="ephnnum"
            name="ephnnum"
            id="ephnnum"
            className='form-control'
            value={data.ephnnum }
            onChange={handleInputChange}
          />
        </div>
        <div className="btn-container7"><button class ="block btn btn-1" onClick={handleSave}>Save</button>
      </div></div></div></div>
    );
}

export default Adminupdateemp