import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Navbar from './Navbar'
const Adminlogin = () =>{
    let Navigate = useNavigate()
    // Declare state variables for email and password
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
  
    // Handle input change events for email and password
    const handleEmailChange = (event) => {
      setEmail(event.target.value);
    };
    const handlePasswordChange = (event) => {
      setPassword(event.target.value);
    };
  
    // Handle login button click event
    const handleLoginClick = (event) => {
      event.preventDefault();
      // Static login credentials for email and password
      const validEmail = "admin@ictak.com";
      const validPassword = "admin12345";
  
      // Compare email and password input to static credentials
      if (email === validEmail && password === validPassword) {
        Navigate("/adminviewemp") 
      } else {
        alert("Invalid email or password.");
      }
    };
  
    return (
      <div>
        <Navbar/>
        <div className="container">
            <div className="loginalign">
            <div className="row g-4">
                <h3>Admin Login</h3>
                <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
          
          <input
            type="email"
            id=""
            className='form-control'
            value={email}
            onChange={handleEmailChange}
            placeholder="Email"
          />
        </div>
                <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
          
          <input
            type="password"
            id=""
            className='form-control'
            value={password}
            onChange={handlePasswordChange}
            placeholder="Password"
          />
        </div>
           
        
        <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
        <div className="btn-container7">
            <button  class ="block btn btn-1" type="button" onClick={handleLoginClick}>
          Login
        </button></div>
        </div>
        </div>
        </div>
        </div>
      </div>
    );
  }
export default Adminlogin