import axios from 'axios'
import React, { useEffect, useState } from 'react'
const moment = require('moment');



const Viewalltrackers = () => {
  const [filterOption, setFilterOption] = useState('daily');
    var [allTrackers,setallTracker]=useState([])
    const [totalTime, setTotalTime] = useState({ hours: 0, minutes: 0, seconds: 0 });
    useEffect(
        ()=>{
            viewallTracker()
        }
        )
        const handleFilterOptionChange = (event) => {
          setFilterOption(event.target.value);
        };
        const filterTrackersByDaily = (trackers) => {
          const today = new Date();
          return trackers.filter((tracker) => {
            const addedDate = new Date(tracker.addeddate);
            return (
              addedDate.getDate() === today.getDate() &&
              addedDate.getMonth() === today.getMonth() &&
              addedDate.getFullYear() === today.getFullYear()
            );
          });
        };
        const filterTrackersByWeekly = (trackers) => {
          const currentWeekStart = moment().startOf('week');
          const filteredTrackers = trackers.filter((tracker) => {
            const trackerDate = moment (tracker.addeddate);
            return trackerDate.isSameOrAfter(currentWeekStart);
          });
          return filteredTrackers;
        };
        
        const filterTrackersByMonthly = (trackers) => {
          const currentMonthStart = moment().startOf('month');
          const filteredTrackers = trackers.filter((tracker) => {
            const trackerDate = moment(tracker.addeddate);
            return trackerDate.isSameOrAfter(currentMonthStart);
          });
          return filteredTrackers;
        };
        
        const filterTrackersByYearly = (trackers) => {
          const currentYearStart = moment().startOf('year');
          const filteredTrackers = trackers.filter((tracker) => {
            const trackerDate = moment(tracker.addeddate);
            return trackerDate.isSameOrAfter(currentYearStart);
          });
          return filteredTrackers;
        };
        
        const viewallTracker = () => {
          const data = {
            userid: sessionStorage.getItem('userid'),
            filterOption: filterOption,
          };
          axios.post('/viewalltrackersapi', data).then((res) => {
            let filteredTrackers = res.data;
            if (filterOption === 'daily') {
              filteredTrackers = filterTrackersByDaily(res.data);
            } else if (filterOption === 'weekly') {
              filteredTrackers = filterTrackersByWeekly(res.data);
            } else if (filterOption === 'monthly') {
              filteredTrackers = filterTrackersByMonthly(res.data);
            } else if (filterOption === 'yearly') {
              filteredTrackers = filterTrackersByYearly(res.data);
            }
            setallTracker(filteredTrackers);
            setTotalTime(getTotalTime(filteredTrackers))
          }).catch((error) => {
            console.log(error);
          });
        };
        const getTotalTime = (trackers) => {
            let totalTime = { hours: 0, minutes: 0, seconds: 0 };
            trackers.forEach((tracker) => {
              totalTime.hours += parseInt(tracker.hours);
              totalTime.minutes += parseInt(tracker.minutes);
              totalTime.seconds += parseInt(tracker.seconds);
            })
    
        }
        let totalTimesec = allTrackers.reduce(
            (totalTime, tracker) =>
              totalTime +
              tracker.hours * 3600 +
              tracker.minutes * 60 +
              tracker.seconds,
            0
          )
        let totalTimehours = parseInt(totalTimesec / 3600);
        totalTimesec = totalTimesec % 3600
        let totalTimeminutes = parseInt(totalTimesec / 60)
        totalTimesec = totalTimesec % 60

        
        
    
  return (
    <div>
      <select  value={filterOption} onChange={handleFilterOptionChange}>
  <option value="daily">Daily</option>
  <option value="weekly">Weekly</option>
  <option value="monthly">Monthly</option>
  <option value="yearly">Yearly</option>
</select>

       
        <table class="table table-hover">
  <thead>
    <tr>
      <th class="table-dark" scope="col">Date</th>
      <th class="table-dark" scope="col">Project</th>
      <th class="table-dark" scope="col">Task</th>
      <th class="table-dark" scope="col">Mode of work</th>
      <th class="table-dark" scope="col">Job description</th>
      <th class="table-dark" scope="col">Working Time</th>
      
    </tr>
  </thead>
  <tbody>
    {allTrackers.map(
        (value,index)=>{
            
            return <tr>
            <td class="table-light">{new Date(value.addeddate).toLocaleDateString('en-GB')}</td>
            <td class="table-light">{value.projectname}</td>
            <td class="table-light">{value.taskname}</td>
            <td class="table-light">{value.mode}</td>
            <td class="table-light">{value.jobdesc}</td>
            <td class="table-light">{value.hours}:{value.minutes < 10 ? '0' + value.minutes : value.minutes}:{value.seconds < 10 ? '0' + value.seconds : value.seconds}</td> 
          </tr>
        }
    )}
    <tr>
          <th class="table-dark" colSpan="5" >Total Time Worked:</th>
          <td class="table-light">{totalTimehours}:{totalTimeminutes < 10 ? '0' + totalTimeminutes : totalTimeminutes}:{totalTimesec < 10 ? '0' + totalTimesec : totalTimesec}
          </td>
        </tr>
   
  </tbody>
</table>
    </div>
  )

}

export default Viewalltrackers