import { useParams } from 'react-router-dom';
import axios from 'axios';
import { useEffect, useState } from 'react';
import Navbaradmin from './Navbaradmin';
import Viewalltrackers from './Viewalltrackers';
import { Navbaradempprofile } from './Navbaradempprofile';

const Profile = () => {
    const [data,setData]= useState([])
    useEffect (
        ()=>{
            fetchProfile()
        }
    )
    const fetchProfile =()=>
{
    const data= {"_id": sessionStorage.getItem("userid")}
    axios.post("/profileapi",data)
    .then(
        (res)=>{
            setData(res.data)
        }
    )
}
   
    return(
        <div>
        <Navbaradempprofile/>
        <div className='container'>
        <div class="row gutters-sm">
            <div class="col-md-4 mb-3">
              <div class="card">
                <div class="card-body">
                  <div class="d-flex flex-column align-items-center text-center">
                    <div class="mt-3">
                      <h4 className='blackTextOverride'>{data.ename}</h4>
                      <p class="text-secondary mb-1">{data.erole}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-8">
              <div class="card mb-3">
                <div class="card-body">
                  <div class="row">
                    <div class="col-sm-3">
                      <h6 class="mb-0">Email</h6>
                    </div>
                    <div class="col-sm-9 text-secondary">
                    {data.eemail}
                    </div>
                  </div>
                  <hr/>
                  <div class="row">
                    <div class="col-sm-3">
                      <h6 class="mb-0">Phone</h6>
                    </div>
                    <div class="col-sm-9 text-secondary">
                    {data.ephnnum}
                    </div>
                  </div>
                
                  
                  
                  
                </div>
              </div>
              </div>
              </div>
       <Viewalltrackers/>
       </div>
        </div>


    )
};

export default Profile;
