import axios from 'axios'
import React, { useState } from 'react'
import { Navbaradempprofile } from './Navbaradempprofile'

const Adminaddemp = () => {
    const [data,setData]=useState(
        {
            ename: "",
              
            erole : "",
              
            eemail: "",
              
            epassword: ""}
    )
    const inputHandler=(event)=>{
        const {name,value}=event.target
        setData(
            (previousState)=>({
            ...previousState,
            [name]:value
        })
        )
    }
    const readValue=()=>{
        console.log(data)
        axios.post("/addempapi",data)
        .then((response)=>{
            console.log(response.data)
            if (response.data.Status == "Success") {
                alert("Employee registered successfully")
                setData({
                    ename: "",
                      
                    erole : "",
                      
                    eemail: "",

                    ephnnum:"",
                      
                    epassword: ""})                
            } else {
                
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    }
  return (
    <div><Navbaradempprofile/>
    <div className="container">
        
        <div className="row">
            <div className="col col-12 col-sm-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
            <div className= "addalign">
                <h3>Add Employee Details</h3>
            <div className="row g-3">
                <div className="col col-12 col-sm-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                    <input 
                    type="text"
                    className="form-control" 
                    placeholder='Employee Name'
                    onChange={inputHandler}
                    value={data.ename}
                    name="ename"
                    />
                </div>
                <div className="col col-12 col-sm-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                    <input
                     type="text"
                     className="form-control" 
                     placeholder='Role'
                     onChange={inputHandler}
                     value={data.erole}
                     name="erole"
                     />
                </div>
                <div className="col col-12 col-sm-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                    <input 
                    type="text"
                    className="form-control" 
                    placeholder='Phone Number'
                    onChange={inputHandler}
                    value={data.ephnnum}
                    name="ephnnum"
                    />
                </div>
                <div className="col col-12 col-sm-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                    <input
                     type="text" 
                     className="form-control" 
                     placeholder='Email'
                     onChange={inputHandler}
                     value={data.eemail}
                     name="eemail"
                     />
                </div>
                
                <div className="col col-12 col-sm-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                <input 
                type="password" 
                 id="" 
                className="form-control" 
                placeholder='Password'
                onChange={inputHandler}
                value={data.epassword}
                name="epassword"
                />

                </div>
                <div className="col col-12 col-sm-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
                    <div className="btn-container7"><button  onClick={readValue} class="block btn btn-1">Submit</button></div>
                </div>
                </div>
            </div>
            </div>
        </div>
    </div></div>
    
  )
}

export default Adminaddemp