import axios from 'axios'
import React, { useEffect, useState } from 'react'

const Empviewalltrackers = () => {


    
    var [allTrackers,setallTracker]=useState([])
    useEffect(
        ()=>{
            viewallTracker()
        }
        )
        const viewallTracker=()=>{
            const data ={"userid":sessionStorage.getItem("userid")}
            axios.post("/viewalltrackersapi",data)
            
            .then(
                (res)=>{
                    
                    setallTracker(res.data)
                }
            ).catch()
        }
    
  return (
    <div>
       
        <table class="table">
  <thead>
    <tr>
      <th scope="col">Date</th>
      <th scope="col">Project</th>
      <th scope="col">Task</th>
      <th scope="col">Mode of work</th>
      <th scope="col">Job description</th>
      <th scope="col">Total time</th>
      <th scope="col">Resume</th>
    </tr>
  </thead>
  <tbody>
    {allTrackers.map(
        (value,index)=>{
            
            return <tr>
            <td>{new Date(value.addeddate).toLocaleDateString('en-GB')}</td>
            <td>{value.projectname}</td>
            <td>{value.taskname}</td>
            <td>{value.mode}</td>
            <td>{value.jobdesc}</td>
            <td>{value.hours}:{value.minutes < 10 ? '0' + value.minutes : value.minutes}:{value.seconds < 10 ? '0' + value.seconds : value.seconds}</td> 
            <td><a href="">Resume</a></td>
          </tr>
        }
    )}
   
   
  </tbody>
</table>
    </div>
  )
}

export default Empviewalltrackers