import React, { useEffect, useState } from 'react'
import axios from 'axios';
import Navbaradmin from './Navbaradmin';
import Timetracker from './Timetracker';
import { useNavigate } from 'react-router-dom';
const Adminviewemp = () => {
  let Navigate = useNavigate()
    const [data,setData]=useState([])
    useEffect(
        ()=>{
            getData();
        }
    )
    
    const getData=()=>{
        axios.post('/adminviewempapi')
        .then((res)=>{
            setData(res.data)
        })
        .catch()
    }
    const handleView = (_id) => {
      sessionStorage.setItem("userid",_id)
      Navigate("/profile")

    }
    const handleUpdate = (_id) => {
      sessionStorage.setItem("userid", _id);
      Navigate("/adminupdateemp");
    }
    const handleDelete = (_id) => {
      if (window.confirm("Are you sure you want to delete this employee?")) {
        // Delete employee
        axios.delete(`/deleteapi/${_id}`)
          .then((res) => {
            if (res.data.Status === "Success") {
              // Delete associated trackers
              axios.delete(`/deletetrackersapi/${_id}`)
                .then((res) => {
                  if (res.data.Status === "Success") {
                    alert("Employee and associated trackers deleted successfully");
                    getData(); // fetch updated data from server
                  } else {
                    alert("Error deleting associated trackers");
                  }
                })
                .catch((error) => {
                  console.log(error);
                  alert("Error deleting associated trackers");
                });
            } else {
              alert("Error deleting employee");
            }
          })
          .catch((error) => {
            console.log(error);
            alert("Error deleting employee");
          });
      }
    };
    
  return (
    <div >
        <Navbaradmin />
        <div className="container">
         
        <h4>&nbsp;</h4>
        <table class="table table-hover" >
  <thead>
    <tr>
      <th class="table-dark" scope="col">Name</th>
      <th class="table-dark" scope="col">Role</th>
      <th class="table-dark" scope="col">Email</th>
      <th class="table-dark" scope="col">Phone Number</th>
      <th class="table-dark" scope="col">View</th>
      <th class="table-dark" scope="col">Update</th>
      <th class="table-dark" scope="col">Delete</th>
    </tr>
  </thead>
  <tbody>
    {data.map(
        (value,index)=>{
            return <tr>
      <th class="table-light" scope="role">{value.ename}</th>
      <td class="table-light">{value.erole}</td>
      <td class="table-light">{value.eemail}</td>
      <td class="table-light">{value.ephnnum}</td>
      <td class="table-light"><a href="" onClick={() => handleView(value._id)}>View</a></td>
      <td class="table-light"><a href="" onClick={() => handleUpdate(value._id)}>Update</a></td>
      <td class="table-light"><a href=""onClick={() => handleDelete(value._id)}>Delete</a></td>
      
    </tr>
        }
    )}
    
    
  </tbody>
</table>
    </div>
    </div>
  )
}

export default Adminviewemp